.wrapper {
    width: 80%;
    display: flex;
    justify-content: space-between;
}

.header {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

/* buttons */
.buttons {
    width: 80%;
    min-width: 30em;
    display: flex;
    justify-content: space-between;
    margin: 1em 0 2em 0 ;
}

.button {
    box-sizing: border-box;
    margin: 0 0.5em;
    border: none;
    border-radius: 5px;
    background-color: var(--btn-color);
    color: var(--white-color);
    padding: 8px 8px;
    font-weight: var(--bold);
}

.button:hover {
    cursor: pointer;
}

/* move buttons */
.moveBtn {
    display: flex;
    align-items: center;

    box-sizing: border-box;
    padding: 4px;

    background-color: transparent;
    border: none;
    color: var(--light-txt-color);
    font-size: 14px;

    margin: 0 0.5em;
}

.moveBtn > svg {
    font-size: 18px;
    margin: 0 4px;
}

.moveBtn:hover {
    cursor: pointer;
    color: var(--main-color);
}

/* inputs */

.edit {
    box-sizing: border-box;
    padding: 0.5em 0.5em;
    margin-bottom: 1em;
    
    width: 80%;
    min-width: 30em;
    background-color: var(--white-color);

    border-radius: 10px;
    border: 1px solid var(--line-color);
}

.title {
    color: var(--txt-color);
    font-size: 1em;
    margin: 0;
}

/* taskNum input */
.header > form {
    margin: 1em 0;
}

.header > form > label {
    font-size: 1em;
    margin-left: 1em;
    color: var(--main-color);
}

.header > form > input {
    box-sizing: border-box;
    margin-left: 1em;

    color: var(--light-txt-color);
    background-color: var(--white-color);
    border-radius: 5px;
    border: 1px solid var(--line-color);
}

.header > form > input:focus {
    border: 1px solid var(--main-color);
    outline: none;
}

/* text area */
.divider {
    width: 100%;
    height: 1px;
    background-color: var(--grey-line-color);
    margin: 4px 0;
}

@media screen and (max-width: 786px) {
    .moveBtn {
        font-size: 14px;
    }
}